import styled from "styled-components"
import { Link } from 'react-router-dom';
export const NavBarSection=styled.div`

padding: 20px 0;
overflow: hidden;
background: #fff;
position: relative;
border-bottom: 1px solid #000
`

export const NavbarLogo=styled.div`

width: 50%;
float: left;
`



export const NavbarLogoText=styled.h2`

font-size: 30px;
font-weight: bold
`


export const NavbarUlList=styled.ul`
width: 50%;
float: left;
list-style: none;
padding: 0;
`

export const NavbarListItem=styled.li`
display: inline-block;
`


export const NavbarListItemAnchor=styled.a`
display: block;
color: #222;
text-decoration: none;
padding: 10px 15px;
font-weight: bold;

&:hover{
    color: #eb5424
}
`


export const MyNavLink = styled(Link)`
display: block;
color: #222;
text-decoration: none;
padding: 10px 15px;
font-weight: bold;

&:hover{
    color: #eb5424
}
`;